import { appConfig } from './config/appConfig';
import { saveState } from './helpers/LocalStorageHelper';
import { RootView } from './components/RootView';
import { render } from 'react-dom';
import 'react-contexify/dist/ReactContexify.min.css';
import './components/Theme.css';
import './index.css';
import { configureStore } from './configureStore';
import * as React from 'react';
import throttle from 'lodash/throttle';
var renderView = function (store) {
    render(React.createElement(RootView, { store: store }), document.getElementById('appRoot'));
};
var store = configureStore();
store.subscribe(function () {
    renderView(store);
});
store.subscribe(throttle(function () {
    if (appConfig.IsSaveStateToLocalStorage) {
        saveState(store.getState());
    }
}, appConfig.SaveStateToLocalStorageInterval));
renderView(store);



